.annotation-image {
	position: relative;
	height: 100%;
	width: 100%;

	display: flex;
	align-items: center;
	justify-content: center;
	-webkit-transform: translate3d(0, 0, 0);

	&__placeholder {
		padding-top: 0.3rem;
	}

	& > img {
		position: relative;
		width: 100%;
		height: 100%;
		object-fit: cover;
		-webkit-transform: translate3d(0, 0, 0);
	}
}
