@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.welcome-screen {
	&__container {
		height: 100%;
		background-color: $color-gray-3;

		display: flex;
		flex-direction: column;
		justify-content: space-between;

		&.--tablet {
			align-items: center;
		}

		&-text {
			&.--tablet {
				display: flex;
				flex-direction: column;
				align-items: center;
			}
		}

		padding: 10.8rem 4rem 9rem;

		&-buttons {
			height: 16rem;
			&.--tablet {
				min-width: 36.2rem;
			}

			display: flex;
			flex-direction: column;
			justify-content: space-between;

			animation-name: buttons-fade-in;
			animation-timing-function: ease;
			animation-duration: 1.2s;
		}
	}
	&__h1 {
		font-size: 4.9rem;
		font-weight: 600;
		line-height: 120%;
		color: $color-white;

		margin-bottom: 3.2rem;

		animation-name: header-fade-in;
		animation-timing-function: ease;
		animation-duration: 0.5s;

		&.--tablet {
			font-size: 6.4rem;
			text-align: center;
		}
	}
	&--text-dark {
		color: $color-gray-2;
	}
	&__separator {
		width: 4.8rem;
		height: 0.1rem;

		margin-bottom: 3.2rem;

		background-color: $color-gray-2;
		border-radius: 0.2rem;
		opacity: 0.5;

		animation-name: hr-fade-in;
		animation-timing-function: ease;
		animation-duration: 0.4s;
	}
	&__p {
		margin-bottom: 3.2rem;

		font-size: 1.8rem;
		font-weight: 500;
		line-height: 155%;
		color: $color-gray-2;

		animation-name: header-fade-in;
		animation-timing-function: ease;
		animation-duration: 0.8s;
	}

	&__demo-data {
		display: flex;
		align-items: center;

		align-self: center;

		margin-top: 1.3rem;
		padding-left: 2rem;

		fill: $color-white;

		color: $color-white;
		font-size: 1.5rem;
		font-weight: 600;

		@include button-press-animation;

		& > div {
			margin-left: 0.4rem;
			padding-top: 0.5rem;
		}
	}
}

@keyframes buttons-fade-in {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes p-fade-in {
	0% {
		opacity: 0;
		transform: translateY(5rem) scale(0.97);
	}
	95% {
		opacity: 0;
		transform: translateY(5rem) scale(0.97);
	}
	100% {
		opacity: 1;
		transform: translateY(0) scale(1);
	}
}

@keyframes hr-fade-in {
	0% {
		opacity: 0;
		transform: translateY(4rem) scale(0.98);
	}
	20% {
		opacity: 0;
		transform: translateY(4rem) scale(0.98);
	}
	100% {
		opacity: 0.5;
		transform: translateY(0rem) scale(1);
	}
}
@keyframes header-fade-in {
	from {
		opacity: 0;
		transform: translateY(5rem) scale(0.98);
	}
	to {
		opacity: 1;
		transform: translateY(0) scale(1);
	}
}
