@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.product-view {
	overflow: scroll;
	height: 100vh;
	background-color: $color-gray-1;

	&__navbar {
		position: absolute;
		z-index: 4;

		width: 100%;
		height: 6.4rem;
		padding: 0 1.6rem;

		display: flex;
		align-items: center;
		justify-content: space-between;

		background-color: transparent;
		color: $color-white;

		transition: background-color 0.1s ease;

		&--full {
			background-color: $color-gray-3;
			& > h1 {
				opacity: 1 !important;
			}
			& > .product-view__icon-back {
				background-color: rgba($color-gray-2, 0.1);
				fill: $color-white;
			}
		}

		& h1 {
			font-size: 1.7rem;
			color: $color-white;
			opacity: 0;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;

			transition: opacity 0.1s ease;
		}

		& > span {
			width: 3.2rem;
		}
	}

	&__icon-back {
		display: inline-block;
		width: 3.8rem;
		min-width: 3.8rem;
		height: 3.8rem;
		margin-right: .1rem;

		// z-index: 4;

		display: flex;
		align-items: center;
		justify-content: center;

		fill: $color-blue-dark;

		border-radius: 50%;
		background-color: $color-white;

		@include icon-press-animation;
	}

	&__image-container {
		height: 35vh;

		display: flex;
		align-items: center;
		justify-content: center;

		overflow: hidden;

		background-color: $color-gray-1;

		fill: white;

		& .annotation-image__placeholder {
			transform: scale(2);
		}

		& .loading-wheel {
			transform: scale(0.5);
			opacity: 0.4;
		}
	}

	&__information {
		padding: 3.2rem 1.6rem 4.2rem;

		background-color: $color-gray-3;
		color: $color-white;

		& > h2 {
			margin-bottom: 0.5rem;

			color: $color-gray-1;
			font-size: 1.5rem;
			font-weight: 500;
			line-height: 140%;
		}
		& > h1 {
			margin-bottom: 1.6rem;

			font-size: 2.8rem;
			line-height: 130%;
		}
		& > p {
			margin-bottom: 2.4rem;

			font-size: 1.6rem;
			line-height: 155%;
			white-space: pre-wrap;
		}
		& > div {
			margin-bottom: 0.8rem;

			font-size: 1.4rem;
			line-height: 155%;

			& > span {
				display: inline-block;
				margin-right: 1.2rem;
				width: 11rem;
			}

			& > b {
				font-size: 1.4rem;
				line-height: 155%;
			}
		}
	}
	&__library {
		padding: 3.2rem 1.6rem 8.2rem;

		background-color: $color-gray-1;
		color: $color-gray-3;

		& > div {
			margin-bottom: 4rem;
		}

		& > h1 {
			margin-bottom: 2.4rem;

			font-size: 2.4rem;
			line-height: 120%;
		}

		& > h2 {
			margin-bottom: 1.2rem;

			font-size: 1.6rem;
			line-height: 155%;
		}
	}
	&__show-more {
		width: 10.5rem;
		height: 3.2rem;

		margin: -2.2rem auto 2.8rem;

		display: flex;
		align-items: center;
		justify-content: center;

		border: 0.1rem solid rgba($color-gray-2, 0.3);
		border-radius: 0.6rem;

		color: $color-blue-dark;
		font-size: 1.3rem;
		font-weight: 600;

		@include button-press-animation;
	}
}
