@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.user-list-item {
	height: 7.6rem;
	padding: 1.2rem 1.2rem;
	margin-bottom: 1rem;

	display: grid;
	grid-template-rows: 2rem 1.7rem;
	grid-template-columns: 4.4rem 1fr 3.2rem;
	grid-row-gap: 0.7rem;
	grid-column-gap: 1.2rem;
	grid-template-areas:
		"profile name icon-call"
		"profile state icon-call";

	border-radius: 0.6rem;
	background-color: $color-white;
	box-shadow: 0px 2px 8px rgba($color-gray-2, 0.05);

	@include button-press-animation;

	&__profile-image {
		grid-area: profile;
		display: inline-block;
		-webkit-transform: translate3d(0, 0, 0);

		margin-top: 0.4rem;
		height: 4.4rem;
		width: 4.4rem;

		background-color: $color-gray-1;
		border-radius: 50%;

		overflow: hidden;

		& .loading-wheel {
			transform: scale(0.3) translateX(.2rem);
			opacity: 0.4;
		}
	}

	&__name {
		font-size: 1.5rem;
		font-weight: 600;
		line-height: 155%;
		color: $color-gray-3;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	&__state-container {
		grid-area: state;
	}

	&__state {
		display: inline;
		padding: 0.2rem 0.4rem;

		background-color: rgba($color-purple, 0.1);
		border-radius: 0.2rem;

		color: $color-purple;
		font-size: 13px;
		font-weight: 500;
	}

	&__icon-call {
		grid-area: icon-call;

		display: inline-block;
		height: 3.2rem;
		width: 3.2rem;

		margin-top: 1.1rem;

		display: flex;
		align-items: center;
		justify-content: center;

		fill: $color-blue-dark;

		background-color: $color-gray-1;
		border-radius: 50%;

		& > div {
			transform: scale(0.8);
		}
	}
}
