@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.equipment-grid-item {
	display: flex;
	justify-content: space-between;
	overflow: hidden;
	align-items: center;
	flex-direction: column;

	border-radius: 0.6rem;
	background-color: $color-white;
	color: $color-gray-3;
	box-shadow: 0px 2px 8px rgba($color-gray-2, 0.05);

	@include button-press-animation;

	&__wrapper {
		position: relative;
		height: 25.3rem;
		flex-basis: 33.3333%;
		width: 32.6rem;
		box-sizing: border-box;
		padding: 0.6rem;
		white-space: nowrap;
		overflow: hidden;

		&.small {
			flex-basis: 50%;
		}
	}

	&__image {
		height: 17.4rem;
		width: 100%;

		display: flex;
		align-items: center;
		justify-content: center;

		fill: $color-white;

		overflow: hidden;

		background-color: $color-gray-1;

		& .loading-wheel {
			transform: scale(0.3);
			opacity: 0.4;
		}
	}

	&__info-wrapper {
		width: 100%;
		margin-left: 1.2rem;
		margin-bottom: 1.6rem;
	}

	&__info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		margin-top: 1.2rem;
	}

	&__type {
		font-size: 1.3rem;
		font-weight: 500;
		line-height: 1.7rem;
		color: $color-purple;
	}

	&__serialnumber {
		font-size: 1.3rem;
		font-weight: 500;
		line-height: 1.7rem;
		color: $color-gray-2;
	}

	&__name {
		font-size: 1.7rem;
		font-weight: 600;
		line-height: 2.3rem;

		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	&__icon-next {
		position: absolute;
		right: 1.2rem;
		bottom: 1.6rem;
	}
}
