@import "styles/variables.scss";
@import "styles/mixins.scss";

.login-screen {
	height: 100%;
	padding: 1.8rem 2.4rem 9rem;
	background-color: $color-gray-3;

	&__navbar {
		display: flex;
		align-items: center;
		justify-content: space-between;

		margin-bottom: 1.8rem;
	}

	&__h1 {
		color: $color-white;
		font-size: 2.2rem;
		line-height: 2.28rem;
	}

	&__icon {
		display: inline-block;
		height: 3.8rem;
		width: 3.8rem;

		background-color: rgba($color-gray-2, 0.1);
		border-radius: 50%;

		display: flex;
		align-items: center;
		justify-content: center;
		@include icon-press-animation;
	}

	&__icon-back {
		fill: $color-white;
	}

	&__icon-qr {
	}

	&__p {
		margin-bottom: 3.3rem;

		color: $color-gray-2;
		font-size: 1.6rem;
		font-weight: 500;
		line-height: 155%;
		text-align: center;
	}

	&__form-container--tablet {
		min-width: 55.4rem;

		&-flex{
			margin-top: 7.8rem;
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}

	&__inputs-container {
		height: 20rem;

		display: flex;
		flex-direction: column;
		justify-content: space-between;

		margin-bottom: 1.4rem;
	}

	&__save-password {
		display: flex;
		justify-content: space-between;
		align-items: center;

		margin-bottom: 3rem;

		color: $color-gray-2;
		font-size: 1.5rem;
		line-height: 155%;
	}
}
