@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.equipment-list-item {
	position: relative;
	height: 8.6rem;
	padding: 0.8rem;
	margin-bottom: 1rem;

	display: flex;

	border-radius: 0.6rem;
	background-color: $color-white;
	color: $color-gray-3;
	box-shadow: 0px 2px 8px rgba($color-gray-2, 0.05);

	@include button-press-animation;

	&__image {
		height: 7rem;
		width: 7rem;
		max-width: 7rem;
		min-width: 7rem;
		min-height: 7rem;
		-webkit-transform: translate3d(0, 0, 0);

		margin-right: 1.6rem;

		display: flex;
		align-items: center;
		justify-content: center;

		fill: $color-white;

		overflow: hidden;

		background-color: $color-gray-1;
		border-radius: 0.6rem;

		& .loading-wheel {
			transform: scale(0.3);
			opacity: 0.4;
		}
	}

	&__info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	&__type {
		font-size: 1.3rem;
		font-weight: 500;
		line-height: 1.7rem;
		color: $color-purple;
	}

	&__serialnumber {
		font-size: 1.3rem;
		font-weight: 500;
		line-height: 1.7rem;
		color: $color-gray-2;
	}

	&__name {
		font-size: 1.7rem;
		font-weight: 600;
		line-height: 2.3rem;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	&__icon-next {
		position: absolute;
		right: 1.2rem;
		bottom: 1.6rem;
	}
}
