@import "~styles/variables.scss";

.success-modal {
	&--visible {
		pointer-events: all;
		background-color: rgba($color-gray-3, 0.4);

		& > .success-modal__popup {
			top: 55vh;
		}
	}

	&__popup {
		display: flex;
		flex-direction: column;
		align-items: center;

		width: 100%;

		padding: 4rem 1.6rem 0;

		background-color: $color-white;

	}

	&__icon-check-circle {
		margin-bottom: 1.6rem;
	}

	&__h1 {
		margin-bottom: 0.8rem;

		color: $color-gray-3;
		font-size: 3.2rem;
		line-height: 120%;
	}

	&__p {
		margin: 0 0.8rem 2.4rem;

		color: $color-gray-3;
		font-size: 1.9rem;
		font-weight: 500;
		line-height: 140%;
		text-align: center;
	}
}
