@import "styles/variables.scss";
@import "styles/mixins.scss";

.button {
	display: flex;
	height: 4.6rem;
	width: 100%;
	align-items: center;
	justify-content: center;

	font-size: 1.6rem;
	font-weight: 600;
	border-radius: 0.6rem;

	color: $color-white;
	background-image: linear-gradient(104.17deg, $color-blue-dark 13.31%, $color-purple 216.3%);

	user-select: none;
	@include button-press-animation;

	&--minimal {
		background-image: none;
		color: $color-blue-dark;
	}

	&--inverted {
		background-image: none;
		background-color: $color-gray-1;
		color: $color-blue-dark;
	}

	&--disabled {
		opacity: 0.4;
		pointer-events: none;
	}

	&--selected {
		background-image: none;
		background-color: #7f86a0;
	}
}
