@import "~styles/mixins.scss";

.step-preview {
	height: 8.6rem;
	background-color: #222936;
	border-radius: 0.6rem;
	display: flex;
	flex-direction: row;
	padding: 1.2rem;
	margin-top: 1rem;
	align-items: center;

	@include button-press-animation;

	&__image-container {
		height: 6.2rem;
		min-width: 6.2rem;
		max-width: 6.2rem;
		border-radius: 0.4rem;
		overflow: hidden;
	}

	&__content {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 4.4rem;
		margin-left: 1.6rem;
	}

	&__index {
		color: #7f86a0;
		font-size: 1.3rem;
	}

	&__title {
		color: white;
		font-size: 1.5rem;
	}
}
