@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.profile-action {
  height: 6.4rem;

  display: flex;
  align-items: center;

  color: $color-gray-3;
  font-size: 1.6rem;
  font-weight: 500;

  border-bottom: .1rem solid $color-gray-1;

  @include button-press-animation;

  &__icon {
    display: inline-block;
    width: 3.2rem; height: 3.2rem;
    margin-right: 1.2rem;

    display: flex;
    align-items: center;
    justify-content: center;

    fill: $color-blue-dark;

    border-radius: 50%;
    background-color: $color-gray-1;
  }
  &__chevron-right {
    display: inline-block;
    width: 1.8rem; height: 1.8rem;
    margin-left: auto;

    fill: $color-purple;
  }
}
