@import "./font.scss";

html {
	height: 100%;

	font-family: "Silka";
	font-size: 10px;

	overflow: hidden;
}

body {
	height: 100%;
	font-size: 1.6rem;
}

#root,
.App {
	height: 100%;
}

* {
	margin: 0;
	padding: 0;

	font-family: inherit;

	box-sizing: border-box;
}

input,
button,
submit {
	border: none;
}

input {
	-webkit-appearance: none;
}

hr {
	border: none;
}

div,
section,
p,
span,
a,
i,
h1,
h2,
h3,
h4,
h5 {
	user-select: none;
}

.react-swipeable-view-container {
	height: 100%;
}

//React SVG creates div that is not centered
i > div {
	display: flex;
	align-items: center;
	justify-content: center;
}

