@import "styles/variables.scss";

.collect-progress {
	display: flex;
	align-items: center;

	margin: 0 1.6rem 0.5rem;

	&__step {
		margin-right: 1.2rem;

		color: $color-purple;
		font-size: 1.5rem;
		font-weight: 600;
		line-height: 130%;
	}

	&__progress-line {
		width: 100%;
		height: 0.2rem;

		background-color: $color-gray-1;

		& > span {
			display: block;

			width: 0;
			height: 0.2rem;

			background-color: $color-purple;

			transition: width .2s ease;
		}
	}
}
