@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.guide-detail {
	display: flex;
	flex: 1;
	min-height: 100vh;
	height: 100vh;
	padding-bottom: 8rem;

	overflow: scroll !important;

	background-color: $color-gray-1;

	top: 0;
	bottom: 0;
	.guide-player{
		width:100%;
	}

	.fullscreen-modal--visible {
		left: auto;
		z-index: 10;
		height: 100vh;
		overflow: scroll;
	}

	&__navbar {
		position: absolute;
		z-index: 5;

		width: 100%;
		height: 6.4rem;
		padding: 0 1.6rem;

		display: flex;
		align-items: center;
		justify-content: space-between;

		background-color: transparent;
		color: $color-white;

		transition: background-color 0.1s ease;

		&--full {
			background-color: $color-gray-3;

			& > h1 {
				opacity: 1 !important;
			}

			& > .guide-detail__icon-back {
				background-color: rgba($color-gray-2, 0.1);
				fill: $color-white;
			}
		}

		& > h1 {
			font-size: 1.7rem;
			color: $color-white;
			opacity: 0;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;

			transition: opacity 0.1s ease;
		}

		& > span {
			width: 3.2rem;
		}
	}

	&__icon-back {
		display: inline-block;
		width: 3.8rem;
		min-width: 3.8rem;
		height: 3.8rem;
		margin-right: .1rem;

		display: flex;
		align-items: center;
		justify-content: center;

		fill: $color-blue-dark;

		border-radius: 50%;
		background-color: $color-white;

		@include icon-press-animation;
	}
}

.guide-detail-tablet {
	.fullscreen-modal--visible {
		width: 67%;
	}
}
