@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.guide-list-item {
	height: 8.6rem;
	padding: 1.2rem;
	margin-bottom: 1rem;

	display: flex;

	border-radius: 0.6rem;
	background-color: $color-white;
	color: $color-black;
	box-shadow: 0px 2px 8px rgba($color-gray-2, 0.05);

	@include button-press-animation;


	&__image {
		-webkit-transform: translate3d(0, 0, 0);
		display: inline-block;

		height: 6.2rem;
		width: 6.2rem;
		min-width: 6.2rem;
		min-height: 6.2rem;

		margin-right: 1.6rem;

		display: flex;
		align-items: center;
		justify-content: center;

		background-color: $color-gray-1;
		fill: $color-white;

		border-radius: 0.6rem;
		overflow: hidden;

		& .loading-wheel {
			transform: scale(0.3);
			opacity: 0.4;
		}
	}

	&__info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;

		color: $color-gray-3;
	}

	&__type {
		margin-bottom: 0.2rem;

		color: $color-purple;
		font-size: 1.3rem;
		font-weight: 500;
		line-height: 1.4rem;
	}

	&__name {
		margin-bottom: 0.4rem;

		font-size: 1.5rem;
		font-weight: 600;
		line-height: 2rem;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	&__steps {
		color: $color-gray-2;
		font-size: 1.3rem;
		line-height: 1.4rem;
		font-weight: 500;
	}
}
