@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.navbar-scroll {
	height: 100%;

	overflow: hidden;

	background-color: $color-gray-1;
	color: $color-white;

	&--uncollapsed {
		& > .navbar {
			height: 14.4rem;
			& .navbar__h1 {
				transform: translate(-5.4rem, 6.1rem) scale(1.35);
			}
			& .navbar__icon-profile {
				width: 3.8rem;
				height: 3.8rem;
			}
		}
		.list-view__search-box {
			top: 14.4rem;
		}
	}
}

.navbar {
	position: fixed;
	top: 0;
	z-index: 2;

	height: 6.4rem;
	width: 100%;

	background-color: $color-gray-3;

	transition: height 0.3s ease;

	&.tablet {
		position: relative;
	}

	&__flexbox {
		height: 6.4rem;
		width: 100%;
		padding: 0 1.6rem;

		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__group {
		width: 100%;
		display: flex;
		align-items: center;
	}

	&__h1 {
		width: calc(100% - 3.8rem - 3.8rem);
		font-size: 2.4rem;
		font-weight: 600;

		transform-origin: top left;

		transition: transform 0.3s ease, font-size 0.3s ease;
	}

	&__icon {
		display: inline-block;
		width: 3.8rem;
		min-width: 3.8rem;
		height: 3.8rem;

		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;

		border-radius: 50%;
		fill: $color-white;

		@include icon-press-animation;

		&-profile {
			position: relative;

			width: 2.8rem;
			min-width: 2.8rem;
			height: 2.8rem;
			margin-right: 2.4rem;

			& * {
				pointer-events: none;
			}

			transition: width 0.3s ease, height 0.3s ease;

			&::after {
				content: "";

				display: inline-block;
				position: absolute;
				right: -0.8rem;
				top: -0.8rem;
				width: 1.8rem;
				height: 1.8rem;

				pointer-events: none;

				background-image: url("~assets/icons/settings.svg");
				background-size: cover;
				background-repeat: no-repeat;
				background-position: center;
			}
		}

		&-wrapper {
			display: inline-block;
			width: 100%;
			height: 100%;
			-webkit-transform: translate3d(0, 0, 0);

			border-radius: 50%;
			overflow: hidden;

			& .loading-wheel {
				transform: scale(0.3);
				opacity: 0.4;
			}
		}

		&-plus {
			display: flex;
			align-items: center;
			justify-content: center;

			margin-right: 1.6rem;

			background-color: rgba($color-gray-2, 0.1);
		}

		&-qr {
			display: flex;
			align-items: center;
			justify-content: center;

			background: $gradient-vertical;
		}
	}

	&__profile-image {
		width: 100%;
		height: 100%;
		border-radius: 50%;

		user-select: none;
		pointer-events: none;

		object-fit: cover;
	}
}

.scroll-view {
	position: relative;
	height: 100%;
	padding-top: 19.6rem;

	overflow-y: scroll;

	&.tablet {
		padding-top: 5.6rem;
		padding-bottom: 5rem;
	}
}
