@import "styles/variables.scss";
@import "styles/mixins.scss";

.collect-step {
	&__toolbar {
		display: flex;
		align-items: center;
		justify-content: space-between;

		margin-bottom: 1.8rem;
	}

	&__content {
		padding: 1.9rem 1.2rem 0;

		margin-bottom: 1.6rem;

		border: .1rem dashed $color-blue-dark;
		border-radius: .4rem;
	}

	&__count {
		color: $color-purple;
		font-size: 1.5rem;
		line-height: 130%;
		font-weight: 600;

		&--warning {
			color: #F1070F;
		}
	}

	&__title {
		font-size: 2rem;
		margin-bottom: 2.4rem;
	}

	&__description {
		margin-bottom: 2rem;

		font-size: 1.5rem;
		line-height: 155%;
		white-space: pre-wrap;
	}

	&__icon-more {
		width: 3.8rem;
		height: 3.8rem;

		display: flex;
		align-items: center;
		justify-content: center;

		border-radius: 50%;
		background-color: rgba($color-gray-1, .5);

		@include icon-press-animation;
	}
}
