@import "~styles/variables.scss";

.list-view {
	background-color: $color-gray-1;

	&__list {
		position: relative;
		padding: 1.6rem 1.6rem 0.8rem;
		transition: padding 0.3s ease;
	}

	&__grid {
		position: relative;
		padding: 1.6rem 1.6rem 0.8rem;
		transition: padding 0.3s ease;
		display: flex;
		flex-flow: row wrap;
		justify-content: flex-start;
		width: 100%;
	}

	&__items-placeholder {
		display: block;

		margin-top: 2rem;

		color: $color-gray-2;
		text-align: center;
	}

	&__search-box {
		position: fixed;
		z-index: 1;
		transition: top 0.3s ease;
		top: 6.4rem;

		display: flex;
		align-items: center;

		width: 100%;
		padding: 1.6rem;

		background-color: $color-gray-1;
	}

	&__search-input {
		width: 100%;
		height: 3.6rem;
		padding-left: 3.2rem;

		border-radius: 0.6rem;
		background-color: $color-gray-1;
		border: 0.1rem solid rgba($color-gray-2, 0.3);

		font-size: 1.5rem;
		font-weight: 500;

		&::placeholder {
			color: $color-gray-2;
		}

		&:focus {
			outline: none;
		}
	}

	&__icon-search {
		position: absolute;
		left: 2.8rem;

		height: 1.8rem;
		width: 1.8rem;

		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__icon-grid-switch{
		margin: 0 0 0 1.6rem;
	}
}
