@import "styles/variables.scss";

.text-input {
	height: 100%;
	min-height: 4.6rem;
	width: 100%;
	padding-left: 1.6rem;

	display: block;

	background-color: transparent;
	border: 0.1rem solid $color-gray-2;
	border-radius: 0.6rem;

	color: $color-white;
	font-size: 1.6rem;
	font-weight: 500;

	&::placeholder {
		color: rgba($color-gray-2, .5);
	}

	&__container {
		min-height: 4.6rem;
		position: relative;

		margin-bottom: 2.3rem;

		&--light {
			& .text-input {
				border-color: rgba($color-gray-2, 0.3);
				color: $color-gray-3;

				&:focus {
					border-color: rgba($color-gray-2, 0.7);
				}
			}

			& .text-input__label {
				background-color: $color-white;
			}
		}
	}

	&:focus {
		outline: none;
	}

	&:focus {
		border-color: $color-white;
	}

	&__label {
		position: absolute;
		top: -1rem;
		left: 1rem;

		padding: 0 0.5rem;

		background-color: $color-gray-3;

		color: $color-gray-2;
		font-size: 1.3rem;
		font-weight: 500;

		pointer-events: none;
	}

	&__password-button {
		position: absolute;
		right: 0;
		top: 0;

		height: 100%;
		width: 6.5rem;

		display: flex;
		align-items: center;
		justify-content: center;

		color: rgba($color-gray-2, 0.8);
		font-size: 1.4rem;
		font-weight: 500;
	}
}

.text-field {
	width: 100%;
	height: 4.6rem;

	padding: 1.2rem 0 1.2rem 1.5rem;

	border: 0.1rem solid rgba($color-gray-2, 0.3);

	color: $color-gray-3;
	font-size: 1.6rem;
	font-weight: 500;
	line-height: 2rem;

	&::placeholder {
		color: rgba($color-gray-2, 0.5);
	}
}
