@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.tabbar {
	position: relative;

	display: flex;
	justify-content: space-between;

	background-color: $color-white;

	box-shadow: 0 -0.4rem 1.6rem rgba($color-gray-2, 0.1);

	&.tablet {
		flex-direction: column;
		width: 10.4rem;
		height: 100%;
		background-color: #222936;
		align-items: center;
		justify-content: center;
	}

	&__swiper {
		position: absolute;
		left: calc(25%);

		transform: translateX(-50%);

		width: 5.6rem;
		height: 0.2rem;

		background: $gradient-horizontal;

		transition: left 0.2s ease;

		&--pos {
			&-0 {
				left: calc(33% / 2);
			}
			&-1 {
				left: calc(50%);
			}
			&-2 {
				left: calc(100% - 33% / 2);
			}
		}
	}

	&__icon {
		display: inline-block;
		width: 50%;
		height: 5.6rem;

		display: flex;
		justify-content: center;
		align-items: center;

		fill: $color-gray-2;

		&--selected {
			fill: $color-purple;
		}

		&.tablet {
			margin-bottom: 8rem;

			&.tabbar__icon--selected {
				fill: $color-white;
			}
		}

		@include icon-press-animation;
	}
}
