@import "../../styles/mixins.scss";

.review-modal {
	padding: 0 4rem;
	display : flex;
	flex-direction: column;
	.information {
		display: flex;
		align-items: center;
		flex-direction: column;
		height: 100%;
	}

	.user-photo {
		padding: 1.7rem 0;
		img {
			display: flex;
			border-radius: 1rem;
			max-height: 15em;
		}
	}

	.title {
		@include h1;
		padding: 0 0 3.2rem;
	}

	.description {
		@include p;
		padding: 0 0 1.6rem 0;
	}

	.buttons {
		display: flex;
		width: 100%;
		padding: 0 0 3.2rem 0;

		.button {
			margin: 0 6px;
		}
		.button--inverted{
			color:#7F86A0;
		}
	}

	.text-input__container {
		width: 100%;
		.text-field {
			height:8rem;
			min-height: 8rem;
			max-height: 8rem;
		}
	}
}
