@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.navbar-select {
	width: 100%;

	@include button-press-animation;

	&__header {
		color: $color-gray-2;
		font-size: 1.3rem;
		font-weight: 600;
		line-height: 130%;
	}

	&__group {
		display: flex;
		align-items: center;

		max-width: 10em;
		overflow:hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		display: block;
	}

	&__icon-up-down {
		width: 1.8rem;

		margin-top: 0.1rem;
		margin-left: 0.8rem;
	}

	&-wrapper{
		display: flex;
		align-items: center;
	}

	& select {
		display: inline;
		appearance: none;
		background-color: transparent;
		border: none;
		padding: 0;
		margin: 0;
		font-family: inherit;
		font-size: inherit;
		color: inherit;
		font-weight: inherit;
		cursor: inherit;
		line-height: inherit;
		outline: none;

		position: absolute;
		top: 0.8rem;

		width: 100%;
		height: 100%;

		opacity: 0;

		&::-ms-expand {
			display: none;
		}
	}
}
