@import "styles/variables.scss";

.field-select {
	position: relative;
	height: 4.6rem;

	display: flex;
	align-items: center;

	margin-bottom: 2.3rem;
	padding-left: 1.6rem;

	border-radius: 0.6rem;
	border: 0.1rem solid rgba($color-gray-2, 0.3);

	&__label {
		position: absolute;
		top: -1rem;
		left: 1rem;

		padding: 0 0.5rem;

		background-color: $color-white;

		color: $color-gray-2;
		font-size: 1.3rem;
		font-weight: 500;

		pointer-events: none;
	}

	&__name {
		color: $color-gray-3;
		font-size: 1.6rem;
		font-weight: 500;

		width: 90%;
		display: block;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;

		& > b {
			color: $color-gray-2;
			font-weight: 500;
		}
	}

	&__icon {
		position: absolute;
		right: 1.4rem;
		top: 1.4rem;
	}

	& select {
		display: inline;
		appearance: none;
		background-color: transparent;
		border: none;
		padding: 0;
		margin: 0;
		font-family: inherit;
		font-size: inherit;
		color: inherit;
		font-weight: inherit;
		cursor: inherit;
		line-height: inherit;
		outline: none;

		position: absolute;
		top: 0rem;
		left: 0rem;

		width: 100%;
		height: 100%;

		opacity: 0;

		&::-ms-expand {
			display: none;
		}
	}
}
