@import "../../styles/mixins.scss";

.pdf-viewer-modal {
	position: relative;
	height: 100%;
	width: 100%;
	overflow-x: hidden;

	.pdf-view {
		position: relative;
		width: 100%;
		height: 100%;
		position: relative;
		overflow-x: hidden;
		overflow-y: auto;

		.pdf-error {
			height: 100%;
			display: flex;
			justify-content: center;
			align-content: center;
			width: 100%;
			margin: 5rem 0 10rem 0;
			font-size: 2rem;
		}

		.close-button {
			height: 1rem;
			width: 100%;
			cursor: pointer;
			background-color: #7F86A0;
		}
	}
}
