.tabbar-navigator {
	&__wrapper {
		height: 100%;
		&--tablet {
			height: 100%;
			display: flex;
			overflow-y: scroll;
		}
	}

	&__profile-wrapper {
		width: 30vw;
	}

	&__collect-wrapper {
		padding-top: 6.7rem;
		background-color: white;
		height: 100%;
		overflow: hidden;
	}

	&__sidebar{
		overflow: hidden;
		max-width: 45rem;
	}

	height: calc(100% - 5.8rem);

	&--tablet {
		height: 100%;
		width: 100%;
	}
}
