@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.guides-list {
	width: 100%;
	min-height: 100vh;
	max-height: 100vh;

	padding: 12rem 0 8rem;
	overflow: scroll !important;

	background-color: $color-gray-1;

	&__navbar {
		position: fixed;
		top: 0;
		z-index: 2;

		width: 100%;
		height: 8rem;

		padding: 0 1.6rem;

		display: flex;
		align-items: center;
		justify-content: center;

		background-color: $color-gray-1;

		& > h1 {
			max-width: 75%;

			font-size: 2rem;

			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
	}

	&__icon-back {
		position: absolute;
		left: 1.6rem;

		display: inline-block;
		width: 3.8rem;
		min-width: 3.8rem;
		height: 3.8rem;

		margin-right: 1.6rem;

		display: flex;
		align-items: center;
		justify-content: center;

		fill: $color-blue-dark;

		border-radius: 50%;
		background-color: rgba($color-gray-2, .1);

		@include icon-press-animation;
	}
}
