@import "~styles/mixins.scss";

.guide-detail-tablet {
	display: flex;
	flex-direction: row;
	overflow: hidden;
	width: 100vw;
	height: 100vh;

	&__side-navigator {
		min-width:33%;
		height: 100%;
		overflow-y: scroll;
		padding-left: 3.2rem;
		padding-right: 3.2rem;
		padding-top: 4.2rem;
		background-color: #2a3344;

		& > h1 {
			display: inline-block;
			width: fit-content;
			font-size: 1.9rem;
			color: white;
			background-color: transparent;
		}
	}

	&__introduction {
		@include button-press-animation;
		margin-top: 2.4rem;
		height: 5.6rem;
		padding-left: 1.6rem;
		padding-top: 1.9rem;
		padding-bottom: 1.9rem;
		background-color: #232936;
		border-radius: 0.6rem;

		color: white;
		font-size: 1.5rem;
		font-weight: 600;
	}

	&__separator {
		height: 0.1rem;
		background-color: #7f86a0;
		//color: #7F86A0;
		margin-top: 2.4rem;
	}

	&__previews-container {
		margin-top: 2.4rem;
		height: 100%;
	}
}
