@import "~styles/variables.scss";

.modalContentWrapper {
	overflow: hidden;
	background-color: white;

	&__button-wrapper {
		overflow: hidden;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		padding-top: 1rem;
		padding-bottom: 1rem;
		padding-right: 1rem;

		color: $color-blue-dark;
		font-size: 1.6rem;
		font-weight: 600;
		line-height: 155%;
	}

	.child-wrapper{
		display: flex;
		height: 100vh;
	}
}
