@import "styles/variables.scss";
@import "styles/mixins.scss";

.photo-picker {
	height: 11.3rem;

	display: flex;
	align-items: center;
	justify-content: center;

	margin-bottom: 2.4rem;

	background-color: $color-gray-1;
	border: 0.1rem dashed $color-blue-dark;
	border-radius: 0.4rem;

	color: $color-blue-dark;
	font-size: 1.6rem;
	font-weight: 600;

	@include button-press-animation;

	&--slim {
		height: 4.7rem;
		border: none;
	}

	&__icon-plus {
		fill: $color-blue-dark;
		margin-right: 0.6rem;
	}

	&__icon-delete {
		position: absolute;
		bottom: 1.2rem;
		right: 1.2rem;

		width: 3.8rem;
		height: 3.8rem;

		display: flex;
		align-items: center;
		justify-content: center;

		padding-bottom: .1rem;

		background-color: $color-white;
		border-radius: 50%;

		@include icon-press-animation;
	}

	&__image-container {
		position: relative;

		height: 35vh;
		margin-bottom: 2.4rem;

		overflow: hidden;

		border-radius: 0.6rem;

		& > img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			position: absolute;
		}
	}
}
