@mixin icon-press-animation {
	transition: opacity 0.1s ease, transform 0.1s ease;

	&:active {
		opacity: 0.6;
		transform: scale(0.8);
	}
}

@mixin button-press-animation {
	transition: transform 0.1s ease, transition 0.1s ease;

	&:active {
		transform: scale(0.98);
		opacity: 0.8;
	}
}

@mixin h1 {
	font-style: normal;
	font-weight: 600;
	font-size: 2.4rem;
	line-height: 2.9rem;
}

@mixin p {
	font-style: normal;
	font-weight: normal;
	font-size: 15px;
	line-height: 23px;
}
