@import "styles/variables.scss";

.collect-screen {
	&--fullscreen {
		padding: 0 !important;
		background-color: $color-gray-1;
		width: 100%;

		& .guide-step__sticky-header {
			top: 0;
		}
	}

	&__content {
		min-height: 100%;

		display: flex;
		flex-direction: column;

		padding: 2rem 1.6rem 5.8rem;
	}

	&__spacer {
		height: 1rem;
		&--big {
			height: 4.8rem;
		}
	}

	&__category {
		margin-bottom: 0.6rem;

		color: $color-gray-2;
		font-size: 1.6rem;
		font-weight: 500;
		line-height: 130%;
	}

	&__title {
		margin-bottom: 1.6rem;

		color: $color-gray-3;
		font-size: 2.7rem;
		font-weight: 600;
		line-height: 120%;

		max-width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	&__description {
		margin-bottom: 3.2rem;

		color: $color-gray-3;
		font-size: 1.6rem;
		font-weight: 500;

		white-space: pre-wrap;

		max-width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	&__end-buttons {
		padding: 0 1.6rem;
		margin-bottom: 3rem;
	}
}
