@import "~styles/variables.scss";
@import "~styles/mixins.scss";

.profile-screen {
	background-color: $color-white;
	height: 100vh;

	&__navbar {
		height: 6.4rem;
		padding: 0 1.6rem;

		display: flex;
		align-items: center;
		justify-content: flex-start;

		background-color: $color-gray-3;
	}

	&__icon-back {
		display: inline-block;
		width: 3.8rem;
		height: 3.8rem;

		display: flex;
		align-items: center;
		justify-content: center;

		border-radius: 50%;
		background-color: rgba($color-gray-2, 0.1);
		fill: $color-white;

		@include icon-press-animation;
	}

	&__jumbotron {
		padding-bottom: 3.2rem;

		display: flex;
		flex-direction: column;
		align-items: center;

		background-color: $color-gray-3;
	}

	&__image {
		width: 12rem;
		height: 12rem;
		margin-bottom: 1.6rem;

		border-radius: 50%;
		-webkit-transform: translate3d(0, 0, 0);
		overflow: hidden;
	}

	&__name {
		margin-bottom: 0.4rem;

		color: $color-white;
		font-size: 1.9rem;
		font-weight: 500;
		line-height: 2.9rem;
	}

	&__position {
		color: $color-gray-2;
		font-size: 1.5rem;
		font-weight: 500;
		line-height: 1.8rem;
	}
	&__actions {
		padding: 0 1.6rem;
	}

	&__app-version {
		display: inline-block;

		width: 100%;
		margin-top: 3rem;

		text-align: center;
		opacity: 0.15;
	}
}
