@import "~styles/variables.scss";
@import "~styles/mixins.scss";

$color-purple-transparent: rgba($color-gray-3, 0.97);

.pinchable-image {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	//z-index must be high number because guide-player is not in the root of App
	z-index: 1201;

	background-color: $color-purple-transparent;

	&:after {
		content: "";
		position: absolute;
		width: 100%;
		height: 10rem;
		background-color: $color-purple-transparent;
	}

	&__wrapper {
		height: 100% !important;
		width: 100% !important;
	}

	&__icon-close {
		position: absolute;
		right: 1.5rem;
		top: 1.2rem;
		z-index: 5;

		width: 4rem;
		height: 4rem;

		background-image: url("~assets/icons/close.svg");
		background-position: center;
		background-repeat: no-repeat;
		background-size: 5rem;

		@include icon-press-animation;
	}
}
